const sendForm = document.getElementById('sendForm')
const gallery = document.querySelector('.gallery')
let baseURL = 'https://api.unsplash.com/photos/'
let searchNameImg = document.getElementById('searchNameImg').value

sendForm.addEventListener('submit', (event) => {
  event.preventDefault()
  makeRequest('GET', baseURL)
    .then(data => renderSearchImg(data))
    .then(err => console.log(err))
})

function makeRequest(method, url) {
  const headers = {
    "Authorization": "Client-ID -dkXdY0X4XMvtNfqESKQXwy3ItkXWHCkVeb-T1EAX_Y",
    'Content-Type': 'application/json;charset=utf-8'
  }
  return fetch(url, {
    method: method,
    headers: headers
  })
    .then(response => {
      return response.json()
    })
}

